html, body {
  background-color: #333; }

h2 {
  text-align: center;
  color: white; }

.memeGenerator {
  max-width: 600px;
  margin: 0 auto; }

.memeContainer {
  position: relative;
  overflow: hidden; }

img {
  width: 100%;
  height: auto; }

.picsMenu img {
  border: 1px solid #333;
  cursor: pointer; }

.controls {
  display: flex;
  justify-content: space-between;
  margin: 15px 0;
  button {
    width: 28%;
    padding: 10px 6px;
    background-color: #333;
    color: white;
    font-family: sans-serif;
    font-size: 16px;
    font-weight: bold;
    text-transform: uppercase;
    border: 2px solid #fff;
    border-radius: 10px;
    outline: none;
    transition: color .2s, background-color .2s;
    @media screen and (max-width: 767px) {
      width: 32%;
      font-size: 14px;
      border-radius: 5px; }
    @media screen and (min-width: 992px) {
      &:hover {
        background-color: #fff;
        color: #333;
        cursor: pointer; } } } }

.caption {
  position: absolute;
  top: 20px;
  cursor: pointer;
  font-family: impact, sans-serif;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 2px 2px 0 #000, -2px -2px 0 #000,2px -2px 0 #000,-2px 2px 0 #000, 0px 2px 0 #000, 2px 0px 0 #000, 0px -2px 0 #000, -2px 0px 0 #000, 2px 2px 5px #000;
  color: white;
  margin: 0;
  user-select: none;
  white-space: pre-line; }

.captionControl {
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
  textarea {
    padding: 10px;
    width: 92%;
    outline: none;
    border: 2px solid #333;
    resize: none;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    transition: all .6s;
    text-transform: uppercase;
    &:focus {
      background-color: #333;
      color: white;
      border-color: white; }
    @media screen and (max-width: 767px) {
      width: 85%;
      padding: 5px;
      font-size: 18px; } }
  input {
    width: 6%;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    border: 2px solid #333;
    outline: none;
    &:focus {
      background-color: #333;
      color: white;
      border-color: white; }
    @media screen and (max-width: 767px) {
      width: 13%;
      font-size: 18px; } } }

.popup {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100vh;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-content: center;
  .imgWrapper {
    max-width: 600px; }
  span {
    color: white;
    font-size: 14px;
    padding-left: 15px; } }

